.nav-link {
    text-decoration: none;
    display: block;
    text-align: center;
    color: #533ca2;
    font-size: 24px;
    position: relative;
    transition: transform .4s, color .4s;

    &:hover {
      transform: scale(1.6);
      color: #381a96;
    }

    &.active {
      transform: scale(1.8);
      color: #381a96;
    }
}



