.theme-button {
	display: block;
	position: relative;
	cursor: pointer;
	border: none;
	background: #533ca2;
	background: -moz-linear-gradient(left, #533ca2 0%, #3c3ca2 100%);
	background: -webkit-linear-gradient(left, #533ca2 0%, #3c3ca2 100%);
	background: linear-gradient(to right, #533ca2 0%, #3c3ca2 100%);
	font-size: 16px;
	text-transform: uppercase;
	line-height: 1;
	padding: 12px 22px;
	font-family: inherit;
	span {
		position: relative;
		display: block;
		color: #fff;
		z-index: 5;
		&:before,
		&:after {
			display: block;
			content: attr(data-glitch);
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			opacity: .8;
			font-weight: 300;
		}

		&:after {
			color: #f0f;
			z-index: -2;
		}

		&:before {
			color: #0ff;
			z-index: -1;
		}
	}

	&:hover {
		span {
			&:before {
				animation: glitch .4s cubic-bezier(.25, .46, .45, .94) infinite reverse
			}

			&:after {
				animation: glitch .4s cubic-bezier(.25, .46, .45, .94) infinite
			}
		}
	}
}

@keyframes glitch {
	0% {
		transform: translate(0)
	}

	20% {
		transform: translate(-2px, 2px)
	}

	40% {
		transform: translate(-2px, -2px)
	}

	60% {
		transform: translate(2px, 2px)
	}

	80% {
		transform: translate(2px, -2px)
	}

	to {
		transform: translate(0)
	}
}