.tag {
    display: inline-block;
    font-size: 14px;
    border: 1px solid #fff;
    padding: 2px 8px 3px;
    color: #fff;
    text-decoration: none;
    transition-property: color, border-color;
    transition-duration: .2s;
    margin: 2px 3px;
    &-dark {
        border-color: #533ca2;
        color: #533ca2;
      
    }
    

    &:not(.active):hover {
        border-color: #a584df;
        color: #a584df;
    }

    &.active {
        color: #533ca2;
        background-color: #fff;
    }

    &-dark.active {
            color: #fff;
            background-color: #533ca2;
        
    }
  
}