.project-preview {
    position: relative;
    width: 100%;
    // margin-bottom: 20px;
    padding: 80px 5%;
    display: flex;
    background: #100642;
    color: #fff;
    overflow: hidden;
    @media (max-width: 1024px) {
        padding: 50px 40px;
    }
    &-name {
        text-transform: uppercase;
        font-size: 50px;
        line-height: 1;
        margin: 0 0 40px;

        @media (max-width: 1024px) {
            font-size: 36px;
        }
        @media (max-width: 656px) {
            font-size: 30px;
            margin-bottom: 24px;
        }
    }

    &-description {
        margin-bottom: 60px;
        @media (max-width: 1024px) {
            font-size: 14px;
            margin-bottom: 40px;
        }
    }

    &-content {
        position: relative;
        z-index: 5;
        min-width: 50%;
        width: 50%;
        @media (max-width: 656px) {
           width: 100%;
        }
    }

    &-tags {
        margin-bottom: 40px;
    }

    &-image {
        position: absolute;
        top: 0;
        right: 5%;
        width: 40%;
        height: 100%;
        display: flex;
        align-items: flex-end;

        img {
            max-width: 100%;
            max-height: 100%;

        }

        // @media (max-width: 656px) {
        //    height: 200px;
        //    right: 0;
        //    top: auto;
        //    left: 50%; 
        //    bottom: 0;
        //  }
         @media (max-width: 656px) {
            height: auto;
            width: 100%;
            left: 0;
            top: 0;
            opacity: .2;
          }
    }

    &.inverted {
        background: #fff;
        color: #533ca2;
        justify-content: flex-end;
    
        .project-preview {
            &-image {
                left: 5%;
                right: auto;
            }
            &-description {
                color: #3c424f
            }
        }
    }
}

