.theme-button {
  display: block;
  position: relative;
  cursor: pointer;
  border: none;
  background: #533ca2;
  background: -webkit-linear-gradient(left, #533ca2 0%, #3c3ca2 100%);
  background: -webkit-gradient(linear, left top, right top, from(#533ca2), to(#3c3ca2));
  background: linear-gradient(to right, #533ca2 0%, #3c3ca2 100%);
  font-size: 16px;
  text-transform: uppercase;
  line-height: 1;
  padding: 12px 22px;
  font-family: inherit; }
  .theme-button span {
    position: relative;
    display: block;
    color: #fff;
    z-index: 5; }
    .theme-button span:before, .theme-button span:after {
      display: block;
      content: attr(data-glitch);
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      opacity: .8;
      font-weight: 300; }
    .theme-button span:after {
      color: #f0f;
      z-index: -2; }
    .theme-button span:before {
      color: #0ff;
      z-index: -1; }
  .theme-button:hover span:before {
    animation: glitch 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite reverse; }
  .theme-button:hover span:after {
    -webkit-animation: glitch 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
            animation: glitch 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite; }

@-webkit-keyframes glitch {
  0% {
    -webkit-transform: translate(0);
            transform: translate(0); }
  20% {
    -webkit-transform: translate(-2px, 2px);
            transform: translate(-2px, 2px); }
  40% {
    -webkit-transform: translate(-2px, -2px);
            transform: translate(-2px, -2px); }
  60% {
    -webkit-transform: translate(2px, 2px);
            transform: translate(2px, 2px); }
  80% {
    -webkit-transform: translate(2px, -2px);
            transform: translate(2px, -2px); }
  to {
    -webkit-transform: translate(0);
            transform: translate(0); } }

@keyframes glitch {
  0% {
    -webkit-transform: translate(0);
            transform: translate(0); }
  20% {
    -webkit-transform: translate(-2px, 2px);
            transform: translate(-2px, 2px); }
  40% {
    -webkit-transform: translate(-2px, -2px);
            transform: translate(-2px, -2px); }
  60% {
