.page-title {
  font-size: 70px;
  text-align: center;
  color: #533ca2;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 10px;
  margin-bottom: 24px;
}

.page-subtitle {
  font-size: 30px;
  text-align: center;
  color: #533ca2;
  text-transform: uppercase;
  font-weight: 300;
  margin-top: 0px;
  margin-bottom: 50px;
  
}

.page-description {
  font-size: 14px;
  text-align: center;
  color: #3c424f;
  font-weight: 300;  
}
.page-tags {
  display: flex;
  max-width: 700px;
  width: 90%;
  margin: 40px auto 20px;
  justify-content: center;
  flex-wrap: wrap;
}
@media (max-width: 500px) {
  .page-title {
    font-size: 40px;
  }
  .page-subtitle {
    font-size: 20px;
  }
}

.accent-color {
  color: #533ca2;
}

.main {
  min-height: 100vh;
}