
.brain {
  position: relative;
  height: 662px;
  width: 600px;
  background-size: contain;
  background-repeat: no-repeat;
  margin: auto;

  &.animated {
    .brain-svg {
      animation: dash 6s forwards; 
    }
    .brain-icon {
      opacity: 1;
    }
    .brain-placeholder {
      opacity: 0;
      transform: scaleX(.01) translateY(90px)
    }
  }

  @media (max-width: 620px) {
    width: 300px;
    height: 331px;
  }
  &-placeholder {
    position: absolute;
    top: 0;
    width: 100%;
    opacity: .4;
    transition: opacity .5s, transform .5s;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
  }

  &-svg {
  
      stroke-dasharray: 2000;
      stroke-dashoffset: 2000;
      @keyframes dash {
        to {
          stroke-dashoffset: 0;
        }
      }
  }
  &-icon {
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0;
    transition: opacity 1s;
    

    &--react {
      height: 10.27%;
      width: 13%;
      left: 8%;
      top: 30%;
      // animation: rotate 10s infinite linear;
    }

    &--vue {
      height: 8.46%;
      width: 10.67%;
      left: 57%;
      top: 8%;
      // animation: blink 4s infinite;
    }

    &--sass {
      height: 8.76%;
      width: 13%;
      left: 53%;
      top: 26%;
      transform: rotateZ(11deg);
    }

    &--git {
      height: 11.48%;
      width: 12.67%;
      left: 66.33%;
      top: 57.7%;
    }

    &--redux {
      height: 11.78%;
      width: 13%;
      left: 29%;
      top: 6%;
    }

    &--webpack {
      height: 10.88%;
      width: 10.67%;
      left: 65%;
      top: 77%;
    }

    &--nodejs {
      height: 9.06%;
      width: 9.33%;
      left: 9%;
      top: 61%;
    }

    &--mongodb {
      height: 11.18%;
      width: 5.33%;
      left: 39%;
      top: 83%;
    }

    &--svg {
      width: 9.67%;
      height: 10%;
      left: 72%;
      top: 35%;
    }

    &--html {
      height: 11.33%;
      width: 12.5%;
      left: 34%;
      top: 38%;
    }

    &--css {
      height: 11.33%;
      width: 12.5%;
      left: 35%;
      top: 60.73%;
    }

    &--js {
      height: 7.85%;
      width: 8.67%;
      left: 53%;
      top: 47%;
    }
  }
}

@keyframes rotate {
  0% {
    transform: rotateZ(0deg);
  }

  100% {
    transform: rotateZ(360deg);
  }
}

@keyframes blink {
  0% {
    opacity: 1
  }

  10% {
    opacity: .3
  }

  20% {
    opacity: 1
  }
}