.wi {
  margin-top: 60px;
  border-top: 1px solid #533ca2;
  font-family: 'Montserrat', monospace;

  &-subtitle {
    font-weight: bold;
    color: #381a96;
    font-size: 20px;
    display: flex;
    margin-bottom: 8px;
    margin-top: 24px;
    &-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      color: #533ca2;
      border: 2px solid  #533ca2;
      // background-color: #533ca2;
      font-size: 12px;
      border-radius: 50%;
      width: 24px;
      height: 24px;
    }
  }

  &-text {
    font-size: 16px;
    @media (max-width: 760px) {
      font-size: 14px;
    }
  }
}