.about {
  &-photo {
    position: relative;
    width: 200px;
    height: 200px;
    overflow: hidden;
    // border-radius: 50%;
    // border: 2px solid #381a96;
    flex-shrink: 0;

    img {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      height: 100%;
    }
  }

  &-container {
    max-width: 900px;
    padding: 0px 10px;
    margin: 0px auto;
  }
  &-description {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    margin-bottom: 30px;
    @media (max-width: 768px) {
      flex-direction: column;
    }
  }
  &-list {
    width: 100%;
    list-style-type: none;
    flex-grow: 1;
    padding: 0;
    font-family: 'Montserrat', monospace;
    font-size: 18px;
    max-width: 520px;
    @media (max-width: 768px) {
      font-size: 14px;
    }
    li {
      margin: 0px 0px 24px;
      display: flex;
      align-items: center;
      
    }
    &-icon {
      display: inline-block;
      color: #381a96;
      width: 30px!important;
      margin-right: 6px;
      @media (max-width: 768px) {
        width: 16px;
      }
    }
    
  }
  &-date {
    width: 144px;
    margin: 0 0 0 auto;
    color:  #381a96;
    font-size: 14px;
    @media (max-width: 768px) {
      font-size: 10px;
      width: 100px;
    }
  }

}

